import OHeader from './components/o-header'
import OFooter from './components/o-footer'
import OSidebar from './components/o-sidebar'

export default {
  name: 'App',
  components: {
    OHeader,
    OFooter,
    OSidebar
  },
  data () {
    return {
      init: false,
      isIndex: true
    }
  },
  watch: {
    $route () {
      this.isIndex = this.$route.name === 'index'
    }
  }
}



